  /* <--- - !| NAVIGATION BAR |! - ----> */

.nav-bar{
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: #050734;
}
.nav-bar-links-holder {
  width: 25%;
  float: right;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  list-style: none;
  color: black;
  margin-top: -15px;
 
}
.nav-bar-logo-holder{ 
  margin-left: 120px;
  font-size: 18pt;
  
}
.nav-bar-logo-symbol, .nav-bar-logo-symbol-right{
  transition: all 0.7s;

 }


.nav-bar-logo-holder:hover > .nav-bar-logo > .nav-bar-logo-symbol {
  animation: open-left 1s linear; 
 }
 .nav-bar-logo-holder:hover >  .nav-bar-logo > .nav-bar-logo-symbol-right{
  animation: open-right 1s linear; 
 }
.nav-bar-logo-holder:hover {
  margin-top: 10px;
  cursor: ponter;
  animation: rotate 5s linear;
 } 
 
 @keyframes open-left{
  50% {
    margin-right: 20px;
  };
  100% {
    margin-right: 0px;
  }
 }
 @keyframes open-right {
  50% {
    margin-left: 20px;
  };
  100% {
    margin-left: 0px;
  }
 }

 @keyframes rotate {
  50% {
    rotate: 180deg;
  };
  100% {
    rotate: 360deg;
  }
 }

.nav-bar-logo-holder{
  margin-top: -19px;
  color: #FCEC3B;
}
.nav-bar-link-item {
  font-family: heebo, sans-serif;
  font-weight: 500;
  font-style: normal;
  transition: all 0.4s;
 }
 .nav-bar-coding-symbol, .nav-bar-coding-symbol-right{
  transition: all 0.7s;
